import React, {Component, Fragment} from 'react';
import {connect} from 'react-redux';
import {withRouter, NavLink} from 'react-router-dom'
import {UIAction} from '../../Modules/UI'
import './styles.css'
import Grid from 'material-ui/Grid';

class Nav extends Component {

  render() {
    const {Language, dispatch} = this.props
    return (<Fragment>
      <Grid item xs={12}>
      <div className="Mobile">
        <div className="row brand-text align-items-center justify-content-around" align="center">
          <div className="col-10">
          <NavLink className="" to="/">
            Mybrand
          </NavLink>
        </div>
          <div className="col-2">

          <select onChange={(e) => {
              dispatch(UIAction({type: 'CHANGE_LANG', payload: e.target.value}))
            }} value={Language}>
            <option value="En">En</option>
            <option value="Fr">Fr</option>
          </select>
        </div>
      </div>
      </div>
    </Grid>
    <Grid item xs={12}>

      <nav className="row align-items-center justify-content-around">
        <div className="col-6">
          <NavLink className="navbar-brand" to="/" align="left">
            Mybrand
          </NavLink>
        </div>
        <div className="col-6">

          <ul className="navbar">
            <li className="nav-item">
              <NavLink to="/">Portfolio</NavLink>
            </li>
            <li>
              <NavLink to="/skills">Skills</NavLink>
            </li>
            <li>
              <NavLink to="/about">Contact</NavLink>
            </li>
            <select onChange={(e) => {
                dispatch(UIAction({type: 'CHANGE_LANG', payload: e.target.value}))
              }} value={Language}>
              <option value="En">En</option>
              <option value="Fr">Fr</option>
            </select>
          </ul>
        </div>
      </nav>
</Grid>
    </Fragment>);
  }
}
const mapStateToProps = (state) => {
  return {Language: state.UI.Language};
};

export default withRouter(connect(mapStateToProps)(Nav));



