// Initial state of the feature
const initialState = {
  Language: 'En',
};

export function UIReducer(state = initialState, action) {
  switch (action.type) {
      case 'CHANGE_LANG':
          return  {
              ...state,
              Language: action.payload
          };
      default:
          return state;
  }
}

export function UIAction(data) {
  switch (data.type) {
      case 'CHANGE_LANG':
          return  {
              type: data.type,
              payload: data.payload
          };
      default:
          return (console.log('no action with that name'));
  }
}



